import MasterPage from '../components/Master';
import fetch from 'isomorphic-unfetch';
import Precio from '../components/Precio';
import Noticias from '../components/Noticias';
import Eventos from '../components/Eventos';

const Index = (props) => (
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del Bitcoin</h2>
                <Precio 
                    precio = {props.precioBitcoin}
                />
            </div>

            <div className="col-md-8">
                <h2>Noticias sobre Bitcoin</h2>
                <Noticias 
                    noticias = {props.noticias}
                />
            </div>

            <div className="col-md-4">
                <h2>Proximos Eventos Bitcoin</h2>
                <Eventos 
                    eventos = {props.eventos}
                />
            </div>
        </div>
    </MasterPage>
);
Index.getInitialProps = async () => {
    //consulta a la api de Precio de Bitcoins
    const precio = await fetch(`https://api.coinmarketcap.com/v2/ticker/1/`);
    const resPrecio = await precio.json();

    //Consulta a la api de Noticias
    const noticias = await fetch(`https://newsapi.org/v2/everything?q=bitcoin&from=2019-07-21&sortBy=publishedAt&apiKey=58192d68532648b4a20a78367350011c&language=es`);
    const resNoticias = await noticias.json();

    //Consulta a la api de Eventos
    const eventos = await fetch(`https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=venezuela&token=HKWNZJCDF4KV3USUJ7FR&locale=es_ES`);
    const resEventos = await eventos.json();

    return {
        precioBitcoin : resPrecio.data.quotes.USD,
        noticias : resNoticias.articles,
        eventos : resEventos.events
    }
}


export default Index;